import React from 'react'
import Property from '../../../../../../../../components/property/Property'
import Title from '../../../../../../../../components/title/Title'
import IExpertise from '../../../../../../../../interfaces/IExpertise.interface'

import styles from './styles/_expertise.module.scss'

function Expertise (props: { expertise: IExpertise, index: number }) {
	const { experience, img, title, column, row } = props.expertise
	const { index } = props

	const gridSettings = {
		gridColumnStart: column.start,
		gridColumnEnd: column.end,
		gridRowStart: row.start,
		gridRowEnd: row.end,
		background: `rgb(${(index + 1) * 10}, ${(index + 1) * 35}, ${(index + 1) * 25})`
	}

	function getImage (img: string) {
		const requiredImage = require(`../../../../../../../../assets/images/proffessions/${img}`)
		return requiredImage
	}

	return (
		<div className="expertise" key={index} style={{ ...gridSettings }}>
			<div className="expertise-image">
				<div className="expertise-image__inner">
					<img src={getImage(img)} alt="Art director" />
				</div>
				<Title className="expertise-image__index" text={ ((index + 1) < 10) ? `0${index + 1}` : `${index + 1}` } />
			</div>
			<div className="expertise-info">
				<div className="expertise-info__title">{title}</div>
				<div className="expertise-info__experience">
					<div className="expertise-info__experience-years">
						<Property text='Experience' />
						<Title className="experience-year__info" text={`${experience.years} years`} />
					</div>
					<div className="expertise-info__experience-portfolio-amount">
						<Property text='Portfolio'/>
						<Title className="experience-portfolio-amount__info" text={`${experience.portfolio_amount}`} />
					</div>
				</div>
			</div>
		</div>
	)
}

export default Expertise
